/* test-slider.css */

/* Styles for the container of the slider */
.test-slider-container {
    margin-bottom: 20px;
  }

  
  /* Styles for the value inside the dot */
  .test-slider-thumb-value {
    font-size: 12px;
    color: #fff;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  #slider-cont {
    width: 100;
  }
  